import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/Layout'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Layout,
      children: [{
        path: '',
        name: 'home-content',
        component: () => import('./views/Articles.vue')
      }]
    },
    {
      path: '/about',
      name: 'about',
      component: Layout,
      children: [{
        path: '',
        name: 'about-content',
        component: () => import('./views/About.vue')
      }]
    },
    {
      path: '/design',
      name: 'design',
      component: Layout,
      children: [{
        path: '',
        name: 'design-content',
        component: () => import('./views/Design.vue')
      }]
    },
    {
      path: '/article',
      name: 'article',
      component: Layout,
      children: [{
        path: '/article/:id',
        name: 'article-content',
        component: () => import('./views/Article.vue')
      }]
    },
    {
      path: '/articles',
      name: 'articles',
      component: Layout,
      children: [{
        path: '',
        name: 'articles-content',
        component: () => import('./views/Articles.vue')
      }]
    },
    {
      path: '/presentation',
      name: 'presentation',
      component: Layout,
      children: [{
        path: '',
        name: 'presentation-content',
        component: () => import('./views/Presentation.vue')
      }]
    }
  ]
})
